* {
	margin: 0;
	padding: 0;
}

.header-section {
	display: flex;
	justify-content: space-between;
	align-items: center;
	align-content: center;
	padding: 0 5%;
	height: 5rem;
	background: rgb(29, 26, 24);
}

.anchor-elements a {
	text-decoration: none;
	font-size: 1.3rem;
	color: rgb(185, 131, 31);
}

.anchor-elements a:hover {
	color: rgb(238, 164, 80);
}

.logo h2 {
	text-transform: uppercase;
	color: rgb(238, 164, 80);
}

.anchor-elements {
	display: flex;
	gap: 2rem;
}
