.img {
	width: 100%;
	object-fit: cover;
	height: 15rem;
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
	overflow: hidden;
}

.img-two {
	width: 100%;
	height: 15rem;
	object-fit: cover;
}

.cards {
	display: flex;
	flex-flow: row wrap;
	justify-content: center;
	gap: 1rem;
}

.card-text {
	text-align: center;
}

.card-one {
	background-color: rgb(228, 206, 182);
	overflow: hidden;
	border-radius: 6px;
	width: 40%;
}

.card-text {
	padding-bottom: 1rem;
}

.cart {
	border-radius: 4px;
	padding: 0.5rem 0;
	text-decoration: none;
}

.cart a {
	text-decoration: none;
	color: white;
	background-color: rgb(228, 134, 44);
	padding: 0.5rem 1.5rem;
}

.card-text h2 {
	padding-bottom: 1rem;
}

@media (max-width: 768px) {
	.anchor-elements a {
		display: none;
	}

	.main h1 {
		font-size: 2rem;
	}

	.card-one {
		width: 90%;
	}
}
